<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <div class="item">
                <label for="specTitle">
                    规格组名<span>*</span>
                </label>
                <span>
                    <input type="text" v-validate="'required'" name="specTitle" id="specTitle" placeholder="请输入规格组名称" v-model="specTitle"/>
                    <i class="tip" v-show="errors.has('specTitle')">{{ errors.first('specTitle') }}</i>
                </span>
            </div>
            <div class="item">
                <label for="specSort">
                    规格排序
                </label>
                <span>
                    <input type="number" name="specSort" id="specSort" placeholder="请输入规格排序[0-9]" v-model="specSort"/>
                </span>
            </div>
        </div>
        <div class="section">
             <div class="item" style="border:0;margin-bottom:1rem;">
                 <label for="specSort">
                    
                 </label>
                 <span class="spec_btn">
                    <button class="button" type="button" @click="addspec">增加规格值</button>
                 </span>
             </div>
             <div class="spec_box noneBor">
                <div class="ggbox" v-for="(item,i) in list" :key="i">
                    <span><i class="red">*</i>规格值</span>    
                    <div class="input-box">
                        <input class="gginput" :data-edit="true" :data-code="item.specificationCode" type="text" v-model="item.option" name="spec" placeholder="请输入规格值" @blur="Type==2?editSpec($event,item):rt()" @focus="Type==2?focusEditSpec($event,item):rt()"/>
                        <input class="gginput1" :data-edit="true" :data-code="item.specificationCode" maxlength="9"  type="number" v-model="item.optionOrders" name="specIndex" placeholder="序号" @blur="Type==2?editStock($event,item):blurInput()" @focus="Type==2?focusEditStock($event,item):rt()"/>
                        <button class="ggbutton" :data-id="item.id" :disabled="list.length<2?true:false" :data-code="item.specificationCode" @click="Type==2?removeSpecSub(item):removeSpec(item)">删除</button>
                    </div>
                    
                </div>
            </div>    
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-if="Type==1" class="button" @click="ok">确定</button>
                <button v-if="Type==2" :disabled="nullTrue" class="button" @click="ok">保存</button>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js'
    export default{
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空",
                        numeric:(field)=>field+"不是数字"
                    },
                    attributes:{
                        specTitle:'规格组名称'
                    }
                }
            };
            Validator.localize(dictionary);
            if(this.$route.query.type!=undefined){
                this.Type=parseInt(this.$route.query.type);
            }
            if(this.$route.query.id!=undefined){
                this.id=parseInt(this.$route.query.id);
            }
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.Type==2){
                that.title="修改规格"
                that.MintUi.Indicator.open({
                    text: '加载中...',
                    spinnerType: 'fading-circle'
                })
                Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.detail, {specificationId: that.id}, 'get',function(data){
                    console.log(data)
                    that.specTitle=data.name;
                    that.specSort=data.orders;
                    var addNum=0;
                    that.list=[];
                    for(let i in data.specificationValues){
                        if(i==data.specificationValues.length-1){
                            addNum++;
                            that.list.push({
                                id:data.specificationValues[i].specificationNameId,
                                option:data.specificationValues[i].specificationValue,
                                optionOrders:data.specificationValues[i].orders,
                                specificationCode:data.specificationValues[i].specificationCode,
                                type:true
                            })
                            break;
                        }else{
                            addNum++;
                            that.list.push({
                                id:data.specificationValues[i].specificationNameId,
                                option:data.specificationValues[i].specificationValue,
                                optionOrders:data.specificationValues[i].orders,
                                specificationCode:data.specificationValues[i].specificationCode,
                                type:true
                            })
                        }
                       
                    }
                    console.log(that.list)
                    that.indexNum=addNum;
                    that.MintUi.Indicator.close();
                });
            }
        },
        data(){
            return {
                token:null,
                Type:1, //编辑类型
                id:null,//规格ID
                title:'添加规格组',
                specTitle:'',//规格标题
                specSort:'',//规格排序
                indexNum:'1',
                nullTrue:false,
                list:[{
                    id:0,
                    option:"",
                    optionOrders:'1'
                }]
            }
        },
        methods:{
            addspec(){
                let that=this;
                this.indexNum++;
                this.list.push({
                    id:new Date().getTime()+''+(Math.random()*900000+100000),
                    option:'',
                    optionOrders:that.indexNum,
                    type:false
                })
            },
            removeSpec(item){
                let that=this;
                let index=com.contains(that.list,item.id,'id');
                that.list.splice(index,1);
            },
            //删除规格值post后台的
            removeSpecSub(item){
                let that=this;
                //删除当前的规格
                var param = {
                    specificationId: item.id,
                    itemValue: item.option,
                    itemCode:item.specificationCode
                };
                if(this.list.length < 2){
                    Exotic.tip({txt:"至少保留一条规格值记录"});
                    return;
                }else{
                    if(item.type==false){
                        let index=com.contains(that.list,item.specificationCode,'specificationCode');
                        that.list.splice(index,1);
                    }else{
                        that.MintUi.MessageBox.confirm('确定删除?').then(function(action){
                            let index=com.contains(that.list,item.specificationCode,'specificationCode');
                            that.MintUi.Indicator.open({
                                text: '删除中...',
                                spinnerType: 'fading-circle'
                            })
                            Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.delete, param, 'post', function (data) {
                                Exotic.tip({txt:data.message});
                                that.MintUi.Indicator.close()
                                that.list.splice(index,1);
                                var u = navigator.userAgent;
                                var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                                var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                                if(isiOS){
                                    //点击保存按钮的时候传递值返回列表
                                    window.location.href = "#SuccessToPrevPage*arg=0";
                                }else if(isAndroid){
                                    window.location.href = "fenzu://jscome/backToPrevPage?update=17";
                                }
                            });
                        },function(err){
                            console.log(err);
                        });
                    }
                };
            },
            focusEditSpec(ev,item){
                let that=this;
                if(item.type==true){
                    //判断是否有商品引用
                    Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.contrast,{
                        spcode:item.specificationCode
                    },'get',function (data) {
                        if(data.isUsed==1){
                            Exotic.tip({txt:'有商品引用，不能编辑！'});
                            ev.target.dataset.edit="false"
                            ev.target.blur()
                        }else if(data.isUsed==0){
                        }
                    })
                }
            },
            editSpec(ev,item){
                let that=this;
                if(item.type==true){
                    let edit=ev.target.dataset.edit;
                    if(edit=="false"){}
                    else if(edit=="true"){
                        Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.edit,{
                            specificationCode:item.specificationCode,
                            orders:ev.target.nextElementSibling.value,
                            specificationValue:ev.target.value
                        },'post',function (data) {
                            if(data.code==0){
                                Exotic.tip({txt:'规格值修改成功!'});
                            }
                        })
                    }
                }
            },
            focusEditStock(ev,item){
                let that=this;
                if(item.type==true){
                    //判断是否有商品引用
                    Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.contrast,{
                        spcode:item.specificationCode
                    },'get',function (data) {
                        if(data.isUsed==1){
                            Exotic.tip({txt:'有商品引用，不能编辑！'});
                            ev.target.dataset.edit="false"
                            ev.target.blur()
                        }else if(data.isUsed==0){
                        }
                    })
                }
            },
            editStock(ev,item){
                let that=this;
                if(item.type==true){
                    let edit=ev.target.dataset.edit;
                    let val=ev.target.value;
                    if(edit=="false"){}
                    else if(edit=="true"){
                        if(isNaN(val)){
                            Exotic.tip({txt:"不是数字"});
                            ev.target.focus();
                            ev.target.value="";
                        }else{
                            Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.edit,{
                                specificationCode:item.specificationCode,
                                orders:val,
                                specificationValue:ev.target.previousElementSibling.value
                            },'post',function (data) {
                                if(data.code==0){
                                    Exotic.tip({txt:'规格值修改成功!'});
                                }
                            })
                        }
                    }
                }
            },
            blurInput(ev){
                let val=ev.target.value;
                if(isNaN(val)){
                    Exotic.tip({txt:"不是数字"});
                    ev.target.focus();
                    this.nullTrue=true;
                }else{
                    this.nullTrue=false;
                }
            },
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj.value){
                        return arr[i];
                    }
                }
            },
            ok(){
                let that=this;
                console.log(this.$data);
                var ins_arr = [];
                var isNotAll = false;
                for (var i = 0; i < that.list.length; i++) {
                    var ins_firt = that.list[i].option;
                    var ins_last = parseFloat(that.list[i].optionOrders);
                    ins_arr.push({
                        option: ins_firt,
                        optionOrders: ins_last
                    });
                    if(ins_firt == '' || ins_last == '') {
                        isNotAll = true;
                    }
                }
                ins_arr = JSON.stringify(ins_arr);
                //判断内容是否为空
                if(that.specTitle == '') {
                    Exotic.tip({txt:"请填写规格组名称"});
                    return false;
                }else if(isNotAll) {
                    Exotic.tip({txt:"请填写规格值"});
                    return false;
                }else {
                    if(that.Type==1){
                        var param = {
                                name: that.specTitle,
                                content: ins_arr,
                                orders: that.specSort
                            };
                            that.MintUi.Indicator.open({
                                text: '提交中...',
                                spinnerType: 'fading-circle'
                            })
                        Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.add,param,'post',function (data) {
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"添加规格成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=15";
                            }
                        });
                    }else if(that.Type==2){
                        let ins_arr = [];
                        let t_arr=[];
                        let isNotAll = false;
                        let isFalse=false;
                        for (var i = 0; i < that.list.length; i++) {
                            if(that.list[i].type==false){
                                var ins_firt = that.list[i].option;
                                var ins_last = parseFloat(that.list[i].optionOrders);
                                ins_arr.push({
                                    option: ins_firt,
                                    optionOrders: ins_last
                                });
                                if(ins_firt == '' || ins_last == '') {
                                    isNotAll = true;
                                }
                            }else{
                                t_arr.push(that.list[i]);
                            }
                        }
                        for (var i = 0; i < that.list.length; i++) {
                            if(that.list[i].type==false){
                                isFalse=true;
                                break;
                            }
                        }
                        for(let i=0;i<t_arr.length;i++){
                            if(ins_arr==[]){
                                break;
                            }
                            for(let k=0;k<ins_arr.length;k++){
                                if(t_arr[i].option==ins_arr[k].option){
                                    ins_arr.splice(k,1)
                                }
                            }
                        }
                        if(isNotAll) {
                            Exotic.tip({txt:"请填写规格值"});
                            return false;
                        }else{
                             let ins_arr_str = JSON.stringify(ins_arr);
                            if(ins_arr==""&&isFalse==true){
                                Exotic.tip({txt:"新增规格重复"});
                            }else{
                                var param = {
                                    specificationId:that.id,
                                    name: that.specTitle,
                                    content: ins_arr_str,
                                    orders: that.specSort
                                };
                                console.log(param);
                                that.MintUi.Indicator.open({
                                    text: '提交中...',
                                    spinnerType: 'fading-circle'
                                })
                                Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.update,param , 'post',function (data) {
                                    that.MintUi.Indicator.close();
                                    Exotic.tip({txt:"修改规格成功"});
                                    var u = navigator.userAgent;
                                    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                                    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                                    if(isiOS){
                                        //点击保存按钮的时候传递值返回列表
                                        window.location.href = "#SuccessToPrevPage*arg=0";
                                    }else if(isAndroid){
                                        window.location.href = "fenzu://jscome/backToPrevPage?update=15";
                                    }
                                });
                            }
                        }
                    }
                }
            }
        },
        mounted(){
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
</style>




